a {
    text-decoration: none;
    color: black;
}

.btn{
    color: white;
    background-color: tomato;
    border-radius: 10px;
    font-size: 20px;
    margin-right: 10px;
    cursor: pointer;
}

.copybtnContainer{
    position: relative;
    display: flex;
    justify-content: end;
    min-width: 904px;
}


.copybtn{
    background-color: #efb4c1;
    color: white;
    font-size: 20px;
    border: none;
    border-top-left-radius: 7px;
    border-top-right-radius: 7px;
}

.copybtn:active{
    color: black;
}

.li{
 list-style: none;   
 margin-top: 15px;
}

li:hover a{
    font-weight: bolder;
    border-bottom: 3px solid tomato;
}


.img{
    margin-left: 10px;
    margin-right: 10px;
    height: 50px;
    vertical-align: middle;
}

.h2{
    text-align: left;
}


.h2:hover span a{
    color: #e48e58;
}

.title{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.titleSvg{
    height: 30px;
    margin-right: 15px;
    cursor: pointer;
}

.textarea{
    min-width: 900px;
    height: 300px;
    border: none;
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;
    border-top-left-radius: 15px;
    font-size: larger;
    position: relative;
}
.textarea:hover{
    border: 4px solid coral;
}

.result{
    margin-top: 15px;
    width: 60%;
    background-color: gray;
    border-radius: 15px;
}

.popup{
    position: absolute;
    width: 180px;
    height: 300px;
    right: 0px;
    background-color: azure;
    border-radius: 10px;
    animation: fadein 5s ease 1s;
    -webkit-animation: fadein 1s; /* Safari and Chrome */
}

.notice {
    position: fixed;
    font-size: 15px;
    text-align : center;
    line-height: 50px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 180px;
    height: 50px;
    background-color: palegreen;
    border-radius: 10px;
    z-index: 999;
  }

@keyframes fadein {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@-webkit-keyframes fadein { /* Safari and Chrome */
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}