.mainContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100%;
  }
  
  .inputBar {
    width: 40%;
    max-width: 450px;
    height: 65px;
    margin-bottom: 10px;
    border-radius: 10px;
    font-size: 30px;
    border: 2px solid #4b89dc;
    text-align: right;
    padding-right: 20px;
    &:focus {
      outline: none;
    }
  }
  
  .buttonContainer {
    display: grid;
    width: 40%;
    max-width: 450px;
    height: 50%;
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 10px;
    grid-row-gap: 10px;
  }
  
  .button {
    background-color: #f2f3f5;
    border: none;
    color: black;
    font-size: 1.5rem;
    border-radius: 35px;
    cursor: pointer;
    box-shadow: 3px 3px 3px lightgray;
  
    &:active {
      margin-left: 2px;
      margin-top: 2px;
      box-shadow: none;
    }
  }
  
  .calButton {
    font-size: 2rem;
    color: white;
    background-color: #4b89dc;
  }
  
  .zeroButton {
    grid-column: 1/3;
  }